.App {
  min-height: 100vh;
  background-color: cadetblue;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

input {
  background-color: cadetblue;
  color: white;
  padding: 1rem;
  border: 0.2px solid;
  border-color: aliceblue;
  border-radius: 3px;
  box-sizing: border-box;
  width: 18rem;
  font-size: 15px;
}


